
.wraper-main[data-v-d55134e0]{
	width:940px;
	margin:0 auto;
	padding:20px 0;
	box-sizing:border-box;
	-webkit-box-sizing: border-box
}
.work-page[data-v-d55134e0] {
	position:relative;
	height:500px
}
.work-page .tab-titles[data-v-d55134e0] {
	position:absolute;
	top:0;
	left:0;
	width:20%;
	z-index:10
}
.work-page .tab-titles li[data-v-d55134e0] {
	width:100%;
	height:60px;
	line-height:60px;
	text-align:center;
	cursor:pointer;
	color:#000;
	font-size:16px;
	border:1px solid #ccc;
	border-bottom:none;
	transition:all .3s linear
}
.work-page .tab-titles li[data-v-d55134e0]:last-of-type {
	border-bottom:1px solid #ccc
}
.work-page .tab-titles li[data-v-d55134e0]:hover {
	border-right-color:#fff
}
.work-page .tab-titles li.active[data-v-d55134e0] {
	color:red;
	border-left-width:10px;
	border-left-color:#000
}
.work-page .tab-contents[data-v-d55134e0] {
	position:absolute;
	top:0;
	left:20%;
	z-index:1;
	width:80%;
	height:500px;
	margin-left:-1px;
	border:1px solid #ccc;overflow: hidden;
}
.work-page .tab-contents .cont-items[data-v-d55134e0] {
	position:absolute;
	top:0;
	left:0;
	display:block;
	width:100%;
	height:100%;
}
.bounce-enter-active[data-v-d55134e0] {
  animation: bounce-in .5s;
}
.bounce-leave-active[data-v-d55134e0] {
  animation: bounce-out .5s;
}
@keyframes bounce-in {
0% {
    transform: translateY(-500px);
}
100% {
    transform: translateY(0);
}
}
@keyframes bounce-out {
0% {
    transform: translateY(0);
}
100% {
    transform: translateY(-500px);
}
}



